@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
}

body {
  background-color: #f2f2f2;
}

#Header {
  background-color: #fff;
  text-align: center;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
}

#Header #HeaderH1 {
  font-size: 30px;
  padding: 10px;
  color: #0400ff;
}

#Wrapper {
  padding: 20px;
  background-color: #fff;
  border-radius: 20px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#WrapperH4 {
  text-align: center;
  margin: 10px;
}

#Courses {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#CourseCard {
  width: 300px;
  height: 300px;
  margin: 10px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid #ebebeb;
}

#CourseCardHeader {
  padding: 8px 25px 8px 25px;
  font-size: 20px;
  background-color: #f2f2f2;
  color: #0400ff;
  display: flex;
  justify-content: space-between;
}

#CourseCardNotesWrapper {
  flex: 1 1;
  padding: 8px;
  overflow: auto;
}

#CourseCardAddNote {
  display: flex;
  padding: 2px;
  justify-content: center;
  align-items: center;
}

#CourseCardAddNote input {
  width: 120px;
  border: 2px solid #ebebeb;
}

#CourseCardNoteInput {
  border-radius: 10px 0px 0px 10px;
  padding: 4px;
}

#CourseCardPercInput {
  border-radius: 0px 10px 10px 0px;
  padding: 4px;
}

#CourseCardNoteButton {
  width: 20px;
  height: 20px;
  border: none;
  margin: 2px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  background-color: #0400ff;
  color: #f1f1f1;
  font-weight: bold;
}

#CourseCardInfo {
  padding: 8px;
  font-size: 15px;
  background-color: #f2f2f2;
  text-align: center;
  font-weight: 600;
}

#AddCourse {
  width: 300px;
  height: 300px;
  margin: 10px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid #ebebeb;
}

#AddCoursePlus {
  font-size: 160px;
  text-align: center;
}

#AddCoursePlus:hover {
  color: #0400ff;
  transition: color 0.2s;
}

#AddCourseDesc {
  text-align: center;
  padding: 10px;
  font-size: 30px;
  color: #0400ff;
}

#Notes #NotesHeader {
  display: flex;
  width: 180px;
  justify-content: space-between;
  font-weight: 600;
}

#NoteData {
  display: flex;
  width: 160px;
  justify-content: space-between;
  margin: 4px;
  padding: 2px;
}

#NoteData p {
  background-color: #f1f1f1;
  padding: 5px;
  width: 50px;
  text-align: center;
  border-radius: 15px;
}

#Note {
  width: 260px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#NoteDelete {
  padding: 5px;
  transition: color 0.2s;
}

#NoteDelete:hover {
  color: red;
}

#InputNewCourse {
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  height: 100vh;
  width: 100vw;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#InputNewCourseInput {
  height: 44px;
  padding: 15px;
  width: 70%;
  max-width: 600px;
  font-size: 20px;
  border-radius: 50px;
}

#InputNewCourseButton {
  height: 44px;
  padding: 2px;
  width: 120px;
  font-size: 20px;
  background-color: #0400ff;
  border: none;
  font-weight: bold;
  color: #fff;
  margin: 5px;
}

#InputNewCourseButtonCancel {
  padding: 2px;
  font-size: 20px;
  color: #0400ff;
  background-color: #000;
  padding: 4px;
  border: none;
  font-weight: bold;
  color: #fff;
  margin: 5px;
}

